import style from "./index.module.css";
import { content } from "./content.js";
import { Link } from "react-router-dom";

function ToSearch(props) {
  const host = window.env.ENV_HOST;
	const text = content[props.lang].text;
	return(
		<Link to={host} className={style.toSearch}>{text}</Link>
	)
}

export default ToSearch;
