import { Link } from "react-router-dom";
import style from './index.module.css';

function CardsWrapper(props) {
	const empty = props.empty;
	const cards = props.cards;
	const more = props.more;
	const contacts = props.contacts;
	const country = props.country;
	const action = props.action;
	const header = props.header;

	const host = window.env.ENV_HOST;

	return (
  	<div className={style.wrapper}>
	  	<section className={style.cards}>
				{	header && <h1>{header}</h1>	}
	  		{
	  			cards.map((card) => 
	  			<div key={card.id} className={style.card}>
	  				<div className={style.image}>
	  					<img src={require(`../../images/logos/${card.id}.png`)} alt={card.header}/>
	  				</div>
	  				<div className={style.text}>
	  					<h2>{card.header}</h2>
	  					<p>{card.desc}</p>
	  					<div className={style.btns}>
	  						<Link to={`${host}card/${country}/${action}/${card.id}`} className={`${style.button} ${style.more}`}>{more}</Link>
	  					</div>
	  				</div>
	  			</div>
	  			)
				}
  			<div key={empty.id} className={style.card}>
  				<div className={style.image}>
  					<img src={require(`../../images/logos/${empty.id}.png`)} alt={empty.header}/>
  				</div>
  				<div className={style.text}>
  					<h2>{empty.header}</h2>
  					<p>{empty.desc}</p>
  					<div className={style.btns}>
  						<Link to={`${host}contacts`} className={`${style.button} ${style.more}`}>{contacts}</Link>
  					</div>
  				</div>
  			</div>
	  	</section>
  	</div>
	)
}

export default CardsWrapper;
